'use client';

import React from 'react';
import { AIReadingPlanGenerator } from '@/components/features/AIReadingPlanGenerator';
import { AIDevotionalGenerator } from '@/components/features/AIDevotionalGenerator';
import { AIBibleStudyHelper } from '@/components/features/AIBibleStudyHelper';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Sparkles, Heart, BookOpen, Lightbulb } from 'lucide-react';

export default function AIToolsPage() {
  return (
    <div className="container mx-auto py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold mb-2 flex items-center gap-2">
          <Sparkles className="h-8 w-8 text-purple-500" />
          AI Tools
        </h1>
        <p className="text-gray-600 dark:text-gray-400">
          Generate personalized content with our free AI tools - no API keys required!
        </p>
      </div>

      <Tabs defaultValue="reading-plans" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="reading-plans" className="flex items-center gap-2">
            <BookOpen className="h-4 w-4" />
            Reading Plans
          </TabsTrigger>
          <TabsTrigger value="devotionals" className="flex items-center gap-2">
            <Heart className="h-4 w-4" />
            Devotionals
          </TabsTrigger>
          <TabsTrigger value="bible-study" className="flex items-center gap-2">
            <Lightbulb className="h-4 w-4" />
            Bible Study
          </TabsTrigger>
        </TabsList>

        <TabsContent value="reading-plans">
          <AIReadingPlanGenerator />
        </TabsContent>

        <TabsContent value="devotionals">
          <AIDevotionalGenerator />
        </TabsContent>

        <TabsContent value="bible-study">
          <AIBibleStudyHelper />
        </TabsContent>
      </Tabs>
    </div>
  );
}
